<template>
    <section class="trending-product section">
        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="section-title">
                        <h2>Products From Fake Store Api</h2>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-3 col-md-6 col-12 mb-4" v-for="(product, key) in products" :key="key">
                    <div class="single-product h-100">
                        <div class="product-image">
                            <img :src="product.image" alt="#" style="height:250px; object-fit:cover;">
                        </div>
                        <div class="product-info">
                            <span class="category">{{ product.category}}</span>
                            <h4 class="title">
                                <router-link :to="{name:'home'}">{{ product.title }}</router-link>
                            </h4>
                            <ul class="review">
                                <li><i class="lni lni-star-filled"></i></li>
                                <li><i class="lni lni-star-filled"></i></li>
                                <li><i class="lni lni-star-filled"></i></li>
                                <li><i class="lni lni-star-filled"></i></li>
                                <li><i class="lni lni-star-filled"></i></li>
                                <li>
                                    <span>{{ product.rating.rate }} Review(s) {{ product.rating.count }}</span>
                                </li>
                            </ul>
                            <div class="price">
                                <span>${{ product.price }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import axios from 'axios';
export default{
    name : 'TrendingProductSection',
    data(){
        return{
            products : null,
        };
    },
    created() {
        axios.get("https://fakestoreapi.com/products").then((response) => {
            this.products = response.data
            // console.warn(response.data);
        });
    },
    methods : {},
};
</script>
